.pagetitle {
  &-top {
    margin-bottom: 15px;
    span {
      text-transform: uppercase;
      font-weight: $fw-bold;
      font-size: $text-14;
      color: $grey-1;
      @include breakpoint(m) {
        font-size: $text-16; } } }
  &-item {
    line-height: 1.3;
    font-size: $text-28;
    margin-bottom: 0;
    @include breakpoint(m) {
      font-size: $text-32; }
    @include breakpoint(l) {
      font-size: $text-40; }
    br {
      @include breakpoint(s-max) {
        display: none; } } } }
