@mixin abs-center {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@mixin all-zero {
  top: 0;
  right: 0;
  bottom: 0;
  left: 0; }

@mixin txt-break() {
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: keep-all;
  line-break: normal;
  hyphens: auto; }

@mixin inlineBreak {
  content: '\A';
  white-space: pre; }
